<template>
  <div class="hello">
    <div class="break-label">Break Length</div>
    <div class="session-label">Session Length</div>
    <button id="break-increment" @click="breakIncrement">break increment</button>
    <button id="break-decrement" @click="breakDecrement">break decrement</button>
    <div id="break-length">break length: {{breakLength}}</div>
    <button id="session-increment" @click="sessionIncrement">session increment</button>
    <button id="session-decrement" @click="sessionDecrement">session decrement</button>
    <div id="session-length">{{sessionLength}}</div>
    <div id="timer-label">{{sessionOrBreakBool ? 'Session' : 'Break'}} {{intervalBool ? 'stopped' : 'initialized'}}</div>
    <div id="time-left">{{addZero(Math.floor(timeLeft / 60))}}:{{addZero(timeLeft % 60)}}</div>
    <audio id="beep"></audio>
    <button id="start_stop" @click="runTimer">start/stop</button>
    <button id="reset" @click="reset">reset</button>
  </div>
</template>

<script>
import { clearInterval, setInterval } from "timers";

export default {
  name: "HelloWorld",
  data() {
    return {
      breakLength: 5,
      sessionLength: 25,
      timeLeft: 69,
      minutes: -1,
      seconds: -1,
      interval: null,
      intervalBool: true,
      sessionOrBreakBool: true,
      audio: null
    };
  },
  methods: {
    reset() {
      this.sessionLength = 25;
      this.timeLeft = 69;
      this.breakLength = 5;

      this.intervalBool = true
      this.sessionOrBreakBool = true

      this.audio.pause()
      this.audio.currentTime = 0
      return clearInterval(this.interval);
    },
    addZero(num) {
      return num < 10 ? `0${num}` : num;
    },
    breakIncrement() {
      return this.breakLength === 60 ? null : this.breakLength++;
    },
    breakDecrement() {
      return this.breakLength === 1 ? null : this.breakLength--;
    },
    sessionIncrement() {
      return this.sessionLength === 60 ? null : this.sessionLength++;
    },
    sessionDecrement() {
      return this.sessionLength === 1 ? null : this.sessionLength--;
    },
    runTimer() {
      if (this.intervalBool) {
        this.audio = new Audio(require('../assets/beep.mp3'))
        this.intervalBool = !this.intervalBool;
        this.interval = setInterval(() => {
          if (this.timeLeft - this.sessionLength <= 0) {
            clearInterval(this.interval);
            this.sessionOrBreakBool = false
            this.interval = setInterval(() => {
              if(this.timeLeft - this.breakLength <= 0) {
                this.audio.play()

                this.timeLeft = 0
                this.intervalBool = true
                return clearInterval(this.interval);
              } else{
                this.timeLeft -= this.breakLength;
              }
            }, 1000);
          } else {
            this.timeLeft -= this.sessionLength;
          }
        }, 1000);
      } else {
        this.intervalBool = !this.intervalBool;
        return clearInterval(this.interval);
      }
    }
  }
};
</script>

